import gsap from "gsap";
import Button from "react-bootstrap/Button";

const Store_Individual_Item = (props) => {
  const setCurrentItemImage = props.setCurrentItemImageProp;
  const setCurrentItemPrice = props.setCurrentItemPriceProp;
  const this_Item = props.this_Item_Prop;
  const this_Item_Image = props.imageProp;
  const this_Item_Price = props.priceProp;
  const this_Item_Title = props.titleProp;
  const index = props.Index;
  const setItemQty = props.setItemQtyProp;
  const setModalVisible = props.setModalVisibleProp;
  const addToCart = props.addToCartProp;

  return (
    <div className="apparel_fade" key={index}>
      <p id="apparel_title">{this_Item_Title}</p>
      <select
        name="selectStock"
        className="item_size_input"
        onChange={(e) => {
          setItemQty(e.target.value);
        }}
      >
        <option value="null">Select Qty-</option>
        <option value="1">1</option>
        <option value="2">2</option>
        <option value="3">3</option>
      </select>
      <img
        src={this_Item_Image}
        className="apparel_piece"
        onClick={() => {
          gsap.from(".myModal_cls", {
            opacity: 0,
            duration: 1,
            height: "0px",
            width: "0px",
          });
          setModalVisible(true);
          setCurrentItemImage(this_Item_Image);
          setCurrentItemPrice(this_Item_Price);
        }}
      ></img>

      <Button
        variant="outline-primary"
        type="button"
        id="cart_add_btn"
        onClick={() => addToCart(this_Item)}
      >
        Add to Cart
      </Button>
      <p id="apparel_price">${this_Item_Price}</p>
    </div>
  );
};

export default Store_Individual_Item;
