import React, {
  useState,
  useEffect,
  useRef,
  useReducer,
  useContext,
} from "react";

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  useHistory,
} from "react-router-dom";

import gsap from "gsap";
import Axios from "axios";
import Forgot_PW_Modal_Container from "./Forgot_PW_Modal_Container";
import { loginAction } from "../REDUX/Actions";
import { setTokenAction } from "../REDUX/Actions";
import { useSelector, useDispatch } from "react-redux";
import { useSpring, animated } from "react-spring";
import Login_Form from "./Login_Form";
import { AnimatePresence, motion } from "framer-motion/dist/framer-motion";
import "../Styles/Login.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const userID = useSelector((state) => state.userID);
  const jwToken = useSelector((state) => state.jwToken);
  const [pwModal, setPWModal] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();

  const user_welcome = () => {
    alert(`Welcome ${jwToken} !!!!!`);
  };

  const closePWModal = () => {
    setPWModal(false);
  };

  const loginUser = () => {
    if (email == null || password == null) {
      alert(
        "Please make sure to enter both an email address AND a password that you have created. Thanks!"
      );
      return;
    } else if (email == "" || password == "") {
      alert(
        "Please make sure to enter both an email address AND a password that you have created. Thanks!"
      );
      return;
    } else {
      setIsLoading(true);
      Axios.post("https://hero-uw.herokuapp.com/login", {
        email,
        password,
      }).then((res) => {
        if (res.data.access_token) {
          console.log(res.data);

          dispatch(loginAction(res.data.username));
          dispatch(setTokenAction(res.data.access_token));
          return history.push("/Store");
        } else {
          setIsLoading(false);
          alert(res.data.msg);
        }
      });
    }
  };

  useEffect(() => {
    gsap.from(".form_slide", {
      x: -100,
      duration: 1,
      opacity: 0,
      delay: 1,
    });
  }, []);

  return (
    <motion.div
      initial={{ opacity: 0, scaleX: 0.7, scaleY: 0.7 }}
      animate={{ opacity: 1, x: 0, scaleY: 1, scaleX: 1 }}
      exit={{ scaleY: 1.5, scaleX: 1.5, opacity: 0 }}
      transition={{ duration: 1 }}
    >
      <div id="home_container">
        <div id="aboutWeeb">
          <video autoPlay muted playsInline loop id="vid_bkgrd">
            <source
              src="Pixabay_lights_-_32299 (Original).mp4"
              type="video/mp4"
            ></source>
          </video>

          {pwModal && <Forgot_PW_Modal_Container modalProp={closePWModal} />}
          <Login_Form
            isLoadingProp={isLoading}
            loginUserProp={loginUser}
            pwModalProp={pwModal}
            closePWModalProp={closePWModal}
            emailProp={email}
            setEmailProp={setEmail}
            passwordProp={password}
            setPasswordProp={setPassword}
            setPWModalProp={setPWModal}
          />
        </div>
      </div>
    </motion.div>
  );
};

export default Login;
